body {
    font-family: arial, sans-serif;
}

.page-title {
    font-size: 2.5rem;
    background-color: white;
    text-align: left;
    padding: 1rem 0;
    color: #ff0048;
}

.container {
    width: 100%;
    height: 70vh;
    overflow: hidden;
}

.editor-container {
    width: 70%;
    height: 100%;
    min-height: 100%;;  
    float: left; 
}

.editor-controls {
    height: 100%;
    width: 30%;
    float: left; 
}
.editor-container iframe {
    height: 100%;
    min-height: 100%;;   
}

.buttons-container {
    padding: 0 2rem;
    margin-top: 1rem;
}

.panels-container {
    display: flex;
    flex-direction: row;
    padding: 0 2rem;
    margin: 1rem 0;
    justify-content: space-between;
}

.layout-item {
    cursor: pointer;
    padding: 0.5rem 1rem;
    margin-left: 0.5rem;
    margin-top: 2px;
    border: 1px solid black;
    text-align: center;
}

.layout-item.selected {
    background-color: #ff0048;
    color: white;
    border-color: #ff0048;
}

.layout-item:hover {
    background-color: black;
    color: white;
}

.left-panel {
    width: 75%;
}

.right-panel {
    width: 25%;
}